import React from "react"
import DownloadButton from '../assets/download.appstore.svg'
import Illustration from '../assets/illustration.svg'
import TitleBodyGroup from '../components/TitleBodyGroup'
import Screenshots3 from '../assets/screenshots.3.png'
import Screenshots2 from '../assets/screenshots.2.png'
import MoodImg from '../assets/mooddata.png'
import FocusImg from '../assets/focusdata.png'
import Logo from '../assets/logo.png'
import Img from 'gatsby-image'
import { graphql } from 'gatsby'

const HeroSection = () => (
  <div className="flex flex-col-reverse sm:flex-row items-center my-0 sm:-my-32 h-screen text-center sm:text-left">
    <div className="flex-1">
      <div className="m-0 sm:mr-2 max-w-lg">
        <h1 className="text-gray-800 font-bold lineHeight-sm text-4xl lg:text-5xl">The modern<br />
          <span className="text-andanteOrange"> practice journal.</span>
        </h1>
        <p className="text-lg text-gray-600 pt-8 pb-10">
        Andante is a minimal but effective practice journal to help musicians achieve calm, focused practice. 
        </p>
        <a className="block w-48 m-auto sm:m-0" href="www.youtube.com">
          <DownloadButton className="downloadButton text-gray-800 fill-current" />
        </a>
      </div>
      
    </div>
    <div className="pb-4 sm:pb-0">
      <Illustration className="w-4/6 sm:w-full max-w-xs m-auto"/>
    </div>
  </div>
)

const Section1 = () => (
  <div className="flex flex-col items-center -mt-10 sm:mt-24 pb-20 text-center">
    <TitleBodyGroup 
        title="Log your sessions." 
        body="Record your sessions live with a timer and practice tools or manually add a session." />
    
    <div className="w-full max-w-2xl">
      <img className="w-full" src={Screenshots3} alt="Screenshots of Andante showing sessions and data"></img>
    </div>
    
    <TitleBodyGroup 
      title="See your progress." 
      body="Use goals, streaks, and beautiful charts to motivate you to keep working hard." />
    
  </div>
)

const Section2 = ( props ) => (
  <div>
    <Img fluid={props.img} />
    <p className="text-center text-sm text-gray-600 pt-4">I use Andante to log my violin practice</p>
  </div>
)

const Section3 = () => (
  <div className="flex flex-col lg:flex-row my-10 sm:my-32 items-center text-center lg:text-left">
    <TitleBodyGroup className="mr-0 mb-10 lg:mr-10 lg:mb-0 w-full"
      title="Track your mood and focus."
      body="Notice patterns in how you feel and how productive you are when you practice." />
    <div className="flex flex-col xs:flex-row">
      <div className="flex-1"><img className="max-w-full px-2 py-2 sm:px-4 lg:p-2" src={MoodImg} alt="A chart showing mood data"></img></div>
      <div className="flex-1"><img className="max-w-full px-2 py-2 sm:px-4 lg:p-2" src={FocusImg} alt="A chart showing focus data"></img></div>
    </div>
    
  </div>
)

const Section4 = () => (
  <div className="flex flex-col-reverse lg:flex-row items-center mt-10">
    <div className=" max-w-lg">
      <img className="" src={Screenshots2} alt="Screenshots of Andante showing journal entries"></img>
    </div>
    <TitleBodyGroup className="my-10 mx-0 lg:ml-10 lg:my-0 flex-1 text-center lg:text-left"
      title="Your thoughts, plans, notes, and ideas - all in one place."
      body="The Journal is a convenient place to write about your practicing." />
  </div>
)

const Section5 = () => (
  <div className="flex flex-col justify-center items-center pt-32 pb-24">
    <img className="w-48 h-48 m-auto" src={Logo}></img>
    <h2 className="font-bold text-4xl text-gray-800 py-8 text-center">Get Andante for free!</h2>
    <a className="block w-48" href="www.youtube.com">
        <DownloadButton className="downloadButton text-gray-800 fill-current" />
    </a>
  </div>
)


const Home = ( props ) => {

  return (
    <div>
      <HeroSection />
      <Section1 />
      <Section2 img={props.data.photo.childImageSharp.fluid} />
      <Section3 />
      <Section4 />
      <Section5 />
    </div>
  )

}

export default Home;

export const pageQuery = graphql`
  query {
    photo: file(relativePath: { eq: "photo.png" }) {
      childImageSharp {
        fluid(maxWidth: 3000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;